html{font-family:"PT Sans","Segoe UI",Optima,Tahoma,Geneva,sans-serif;font-size:62.5%;color:#333;background-color:#ccc;height:100%}body{margin:0;display:flex;flex-direction:column;font-size:1.6rem;min-height:100%;min-width:320px}@media screen and (max-width: 600px){body{font-size:1.4rem}}h3,h4{font-family:"PT Serif",Baskerville,Georgia,serif;font-weight:700}header{background-color:#4c6e8d;color:#fff}h1{margin:0;font-size:2rem;line-height:2rem}.logo{vertical-align:top}.logo-beta{font-weight:normal}header,section{padding:1.5rem 3rem}.content-container{flex:1;display:flex;flex-wrap:wrap}section{flex:1}#instructions{min-width:250px}#results{min-width:320px;background-color:#fff}h2{margin:.8rem 0;font-size:inherit;text-transform:uppercase}h3{margin:.5rem 0;font-size:2.4rem}h4{margin:.8rem 0}.errors{position:absolute}.form-message{max-width:30rem;padding:.2rem 2rem;margin-top:1rem;border-top:2px solid #282;background-color:#aea}.form-message p,.form-message pre{margin:.5rem 0}.form-error{border-top-color:red;background-color:#fcc}.pre-wrap{white-space:pre-wrap;white-space:-moz-pre-wrap;word-wrap:break-word}.appearance-textfield{-webkit-appearance:none;-moz-appearance:none;appearance:none;padding:1px;background-color:#fff;width:8rem;border:1.5px solid #4c6e8d;font:inherit;resize:none;border-radius:0;overflow-x:hidden}.appearance-textfield:disabled{border-color:#8999a8;background-color:#eee;color:#555}button{padding:.8rem 1.6rem;background-color:#4c6e8d;border:none;color:#fff;font-size:1.8rem;font-family:inherit;cursor:pointer}.full-width{box-sizing:border-box;display:block;width:100%}.icon-button{float:right;padding:0 .8rem;background:none;color:#333}.rowbox-container{display:flex;justify-content:space-evenly;flex-wrap:wrap}.rowbox-box{flex:1 0;min-width:15rem;max-width:30rem;padding:0 1.6rem;margin:.8rem;background-color:#f0f8ff}.rowbox-wide{min-width:20rem}.rowbox-content,.rowbox-content p,.rowbox-content pre{margin:.8rem 0}.stats-list{list-style-type:none;padding:0;marg